import styled, { css } from 'styled-components';
import Link from 'gatsby-link';

const ShowcaseOverlay = styled.div`
  display: flex;
  position: fixed;
  bottom: 0;
  min-width: 100%;
  min-height: 100%;
  width: auto;
  height: auto;
  z-index: -100;
  left: 50%;
  transform: translateX(-50%);
  background: #263238;
  z-index: -99;
  opacity: 0.8;
`;

const ShowcaseVideo = styled.video.attrs({
  preload: true,
  autoPlay: true,
  loop: true,
})`
  display: flex;
  position: fixed;
  bottom: 0;
  min-width: 100%;
  min-height: 100%;
  width: auto;
  height: auto;
  z-index: -100;
  left: 50%;
  transform: translateX(-50%);
`;

const SiteTitle = styled.div`
  margin-top: 40px;
`;

const SubTitle = styled.div`
  padding-left: 110px;
  font-size: 20px;
  margin: 0 20px;
`;

const Main = styled.main`
  max-width: 960px;
  margin: 60px auto;
  min-height: 70vh;
  padding: 100px 5% 80px;
  color: #FFF;
`;

const MainWrapper = styled.div`
  a {
    color: ${props => props.color};
  }
`;

const SiteHeader = styled.header`
  display: flex;
  justify-content: space-between;
  position: fixed;
  flex-wrap: wrap;
  top: 0;
  width: 100%;
  &:hover {
    h1 {
      transform: translate3d(80px, 0, 0);
      transition: 0.3s all cubic-bezier(0.165, 0.84, 0.44, 1);
    }
  }
`;

const SiteNav = styled.nav`
  > ul {
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
    > li {
      margin: 0;
    }
  }
`;

const NavLink = styled(Link)`
  text-decoration: none;
  color: #fff;
  font-size: 14px;
  padding: 15px 10px 18px;
  font-weight: 900;
  text-transform: uppercase;
  letter-spacing: 5px;
  display: block;
  transition: all 0.3s ease;
  &:hover {
    background-color: ${props => props.background};
  }
`;

export {
  ShowcaseVideo,
  ShowcaseOverlay,
  SiteTitle,
  SubTitle,
  MainWrapper,
  Main,
  SiteHeader,
  SiteNav,
  NavLink,
};



